<template>
  <div>
    <b-card class="mb-1">
      <b-card-title class="mb-1">
        Reporte de asistencia
      </b-card-title>
      <b-card-sub-title class="mb-2">
        Seleccione el intervalo de fechas para calcular el reporte
      </b-card-sub-title>
      <b-row>
        <b-col
          xl="6"
          lg="6"
          md="6"
          sm="12"
        >
          <label for="example-datepicker">Fecha Inicio</label>
          <b-form-datepicker
            v-model="fechaInicio"
            class="mb-1"
            @input="refreshTabla"
          />
        </b-col>
        <b-col
          xl="6"
          lg="6"
          md="6"
          sm="12"
        >
          <label for="example-datepicker">Fecha Fin</label>
          <b-form-datepicker
            v-model="fechaFin"
            class="mb-1"
            @input="refreshTabla"
          />
        </b-col>
      </b-row>
    </b-card>
    <b-card>
      <b-row>
        <b-col
          xl="4"
          lg="4"
          md="4"
          sm="12"
        >
          <donut-chart
            :array-asistidos="reporteGeneralDesayuno"
            :nombre-comida="'Desayuno'"
          />
        </b-col>
        <b-col
          xl="4"
          lg="4"
          md="4"
          sm="12"
        >
          <donut-chart
            :array-asistidos="reporteGeneralAlmuerzo"
            :nombre-comida="'Almuerzo'"
          />
        </b-col>
        <b-col
          xl="4"
          lg="4"
          md="4"
          sm="12"
        >
          <donut-chart
            :array-asistidos="reporteGeneralCena"
            :nombre-comida="'Cena'"
          />
        </b-col>
      </b-row>

      <b-row>
        <b-col
          cols="7"
          class=""
        >
          <label>Mostrar</label>
          <b-form-select
            id="per-page-select"
            v-model="perPage"
            :options="pageOptions"
            size="sm"
            class="per-page-selector d-inline-block mx-50"
            style="width:auto"
            @change="actualizar_to_from"
          />
          <label>registros</label></b-col>

        <b-col
          cols="5"
          class="mb-1"
        >
          <b-form-group
            label=""
            label-for="filter-input"
            label-cols-sm="3"
            label-align-sm="right"
            class="mb-0"
          >
            <b-input-group>
              <b-form-input
                id="filter-input"
                v-model="filter"
                type="search"
                placeholder="Buscar..."
                size="sm"
              />

              <b-input-group-append>
                <b-button
                  :disabled="!filter"
                  variant="success"
                  size="sm"
                  @click="filter = ''"
                >
                  Limpiar
                </b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group></b-col>
      </b-row>
      <b-table
        :items="becarios"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"

        head-variant="light"
        class="position-relative"
        show-empty
        hover
        bordered
        responsive
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        @filtered="onFiltered"
      >

        <template #thead-top>
          <b-tr>
            <b-th
              colspan="2"
              class="text-center"
            >
              <span class="text-primary">Becarios</span>
            </b-th>
            <b-th
              class="text-center bg-light-primary"
              colspan="2"
              variant="secondary"
            >
              <span class="text-primary">Desayuno</span>
            </b-th>
            <b-th
              class="text-center bg-light-warning"
              variant="primary"
              colspan="2"
            >
              <span class="text-warning">Almuerzo</span>
            </b-th>
            <b-th
              class="text-center bg-light-danger"
              colspan="2"
              variant="danger"
            >
              <span class="text-danger">Cena</span>
            </b-th>
          </b-tr>
        </template>
        <template #cell(index)="row">
          {{ from + row.index }}
        </template>
        <template #cell(becario)="row">
          {{ row.item.persona.apellidos }} {{ row.item.persona.nombres }}
        </template>
        <template #cell(d_asistio)="row">
          {{ row.item.desayuno.asistio }}
        </template>
        <template #cell(d_no_asistio)="row">
          {{ row.item.desayuno.no_asistio }}
        </template>
        <template #cell(a_asistio)="row">
          {{ row.item.almuerzo.asistio }}
        </template>
        <template #cell(a_no_asistio)="row">
          {{ row.item.almuerzo.no_asistio }}
        </template>
        <template #cell(c_asistio)="row">
          {{ row.item.cena.asistio }}
        </template>
        <template #cell(c_no_asistio)="row">
          {{ row.item.cena.no_asistio }}
        </template>

      </b-table>
      <b-row
        cols="12"
        class="d-flex justify-content-between my-1 mb-md-0"
      >
        <b-col cols="6">
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
        </b-col>
        <b-col cols="4">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="fill"
            size="sm"
            class="my-0"
            @input="actualizar_to_from"
          />
        </b-col>

      </b-row>
    <!-- <b-table
      :items="items"
      :fields="fields"
      responsive="sm"
    >
      <template #thead-top="data">
        <b-tr>
          <b-th colspan="2"><span class="sr-only">Name and ID</span></b-th>
          <b-th variant="secondary">Type 1</b-th>
          <b-th variant="primary" colspan="3">Type 2</b-th>
          <b-th variant="danger">Type 3</b-th>
        </b-tr>
      </template>
    </b-table> -->

    </b-card>
  </div>
</template>

<script>
import {
  BCard, BRow, BCol, BFormDatepicker, BCardTitle, BCardSubTitle, BTr, BTh,
  BFormSelect, BButton, BFormGroup, BFormInput, BInputGroup, BPagination, BTable, BInputGroupAppend,
} from 'bootstrap-vue'
import store from '@/store'
import { onUnmounted } from '@vue/composition-api'
// import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// import FeatherIcon from '@/@core/components/feather-icon/FeatherIcon.vue'
// import VueApexCharts from 'vue-apexcharts'
import reporteStoreModule from './reporteStoreModule'
import DonutChart from './DonutChart.vue'

import apexChartData from './apexChartData'
// import { onUnmounted } from '@vue/composition-api'
// import ModalFormularioConvocatoria from './ModalFormularioConvocatoria.vue'
// import postulacionStoreModule from './postulacionStoreModule'

export default {
  components: {
    BTr,
    BTh,
    BCard,
    BFormDatepicker,
    BCardTitle,
    BCardSubTitle,
    // VueApexCharts,
    // BCard,
    // BDropdown,
    // BDropdownItem,
    // BBadge,
    BFormSelect,
    BButton,
    BFormGroup,
    BFormInput,
    BRow,
    BCol,
    BInputGroup,
    BPagination,
    BTable,
    BInputGroupAppend,

    // BTableSimple,
    // BTr,
    // BTd,
    // BButton,
    // BTh,
    // BRow,
    // BCol,
    // BFormGroup,
    // BFormCheckboxGroup,
    DonutChart,
  },
  data() {
    return {
      apexChartData,
      reporteGeneralDesayuno: [100],
      reporteGeneralAlmuerzo: [100],
      reporteGeneralCena: [100],
      fechaInicio: '2021-03-02',
      fechaFin: '2021-03-02',
      // comidasSelect: ['desayuno', 'almuerzo', 'cena'],
      // optionsComidas: [
      //   { item: 'desayuno', name: 'Desayuno' },
      //   { item: 'almuerzo', name: 'Almuerzo' },
      //   { item: 'cena', name: 'Cena' },
      // ],

      becarios: [],
      cursoTallerUpdate: {},
      tallerGlobal_nombre: '',
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
      totalRows: 0,
      to: 0,
      from: 0,
      actionTaller: false,
      fields: [
        { key: 'index', label: 'N°', sortable: true },
        { key: 'becario', label: 'BECARIO', sortable: true },
        { key: 'd_asistio', label: 'A', sortable: true },
        { key: 'd_no_asistio', label: 'NA', sortable: true },
        { key: 'a_asistio', label: 'A', sortable: true },
        { key: 'a_no_asistio', label: 'NA', sortable: true },
        { key: 'c_asistio', label: 'A', sortable: true },
        { key: 'c_no_asistio', label: 'NA', sortable: true },
      ],
    }
  },
  created() {
    this.refreshTabla()
  },
  methods: {
    async refreshTabla() {
      const DATA = {
        fecha_inicio: this.fechaInicio,
        fecha_fin: this.fechaFin,
      }
      console.log('DATA:', DATA)
      await store.dispatch('reportes-comedor-store-module/getReporte', DATA).then(response => {
        console.log('RESPONSE: ', response)
        this.becarios = response.data.becarios
        this.totalRows = response.data.becarios.length

        this.reporteGeneralDesayuno = [response.data.general.desayuno.asistio, response.data.general.desayuno.no_asistio]
        if (response.data.general.desayuno.asistio === 0 && response.data.general.desayuno.no_asistio === 0) {
          this.reporteGeneralDesayuno = [100]
        }
        // if (this.reporteGeneralDesayuno === [0, 0]) { this.reporteGeneralDesayuno = ['100'] }
        this.reporteGeneralAlmuerzo = [response.data.general.almuerzo.asistio, response.data.general.almuerzo.no_asistio]
        if (parseInt(response.data.general.almuerzo.asistio, 10) === 0 && parseInt(response.data.general.almuerzo.no_asistio, 10) === 0) {
          this.reporteGeneralAlmuerzo = [100]
        }
        // if (this.reporteGeneralAlmuerzo === [0, 0]) { this.reporteGeneralAlmuerzo = ['100'] }
        this.reporteGeneralCena = [response.data.general.cena.asistio, response.data.general.cena.no_asistio]
        if (response.data.general.cena.asistio === 0 && response.data.general.cena.no_asistio === 0) {
          this.reporteGeneralCena = [100]
        }
        console.log('message:', this.reporteGeneralDesayuno)
        // if (this.reporteGeneralAlmuerzo === [0, 0]) { this.reporteGeneralAlmuerzo = ['100'] }
        this.actualizar_to_from()

        // apexChartData.donutChart.chartOptions = [30, 60, 40]
      })
    },
    actualizar_to_from() {
      const { totalRows } = this
      if (totalRows === 0) {
        this.from = 0
        this.to = 0
      } else
      if (this.currentPage === 1) {
        this.from = 1
        if (totalRows <= this.perPage) {
          this.to = totalRows
        } else {
          this.to = this.perPage
        }
      } else {
        this.from = ((this.currentPage - 1) * this.perPage) + 1
        if ((this.currentPage * this.perPage) <= totalRows) {
          this.to = this.currentPage * this.perPage
        } else {
          this.to = totalRows
        }
      }
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
      this.actualizar_to_from()
    },
  },
  setup() {
    const REPORTE_CALCULAR_APP_STORE_MODULE_NAME = 'reportes-comedor-store-module'

    // Register module
    if (!store.hasModule(REPORTE_CALCULAR_APP_STORE_MODULE_NAME)) store.registerModule(REPORTE_CALCULAR_APP_STORE_MODULE_NAME, reporteStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(REPORTE_CALCULAR_APP_STORE_MODULE_NAME)) store.unregisterModule(REPORTE_CALCULAR_APP_STORE_MODULE_NAME)
    })
    // const resolveTallerStatusVariant = estado => {
    //   if (estado === 1) return 'success'
    //   if (estado === 0) return 'danger'
    //   return 'primary'
    // }

    return {
      // resolveTallerStatusVariant,
    }
  },
}
</script>

<style>
</style>
